<template>
  <div class="container">
    <header class="header"></header>
    <div class="content" id="content">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-swipe :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="(item, index) of bannerlist" :key = "index">
            <img :src="item" alt="">
          </van-swipe-item>
        </van-swipe>
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text=""
          @load="onLoad"
        >
          <Prolist :prolist = "prolist"/>
        </van-list>
      </van-pull-refresh>
      <Backtop :show="flag"/>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Swipe, SwipeItem, List, PullRefresh } from 'vant'
import Prolist from '@/components/common/Prolist'
import Backtop from '@/components/Backtop'

Vue.use(Swipe).use(SwipeItem)
Vue.use(List)
Vue.use(PullRefresh)
export default {
  data () {
    return {
      bannerlist: [],
      prolist: [],
      loading: false, // loadloadingtrue
      finished: false, // true
      pageNum: 1, // 
      isLoading: false,
      flag: false
    }
  },
  methods: {
    onRefresh () {
      this.isLoading = true
      fetch('https://www.daxunxun.com/douban').then(res => res.json()).then(data => {
        this.isLoading = false // 
        this.prolist = data // 
        this.pageNum = 1 //  --- 
        this.finished = false // 
      })
    },
    onLoad () {
      console.log('')
      this.loading = true // 
      fetch('https://www.daxunxun.com/douban?count=20&start=' + this.pageNum * 20)
        .then(res => res.json()).then(data => {
          this.loading = false // 
          this.pageNum++
          if (data.length === 0) {
            console.log('')
            this.finished = true // 
          } else {
            //    concat
            this.prolist = [...this.prolist, ...data]
          }
        })
    },
    scrollFn () {
      console.log(event.target.scrollTop)
      if (event.target.scrollTop > 150) {
        this.flag = true
      } else {
        this.flag = false
      }
    }
  },
  components: {
    Prolist, // Prolist: Prolist
    Backtop
  },
  mounted () {
    //  --- axiosaxios
    fetch('https://www.daxunxun.com/banner').then(res => res.json()).then(data => {
      var arr = []
      data.map(item => { // 
        item = 'https://www.daxunxun.com' + item
        arr.push(item)
      })
      console.log(arr)
      this.bannerlist = arr
    })
    // 
    fetch('https://www.daxunxun.com/douban').then(res => res.json()).then(data => {
      console.log(data)
      this.prolist = data
    })
    // DOM
    const content = document.querySelector('#content')
    // 
    content.addEventListener('scroll', this.scrollFn)
  },
  beforeRouteLeave (from, to, next) { //  --- 
    const content = document.querySelector('#content')
    content.removeEventListener('scroll', this.scrollFn)
    let position = content.scrollTop
    localStorage.setItem('position', position)
    next() // 
  },
  watch: {
    $route (newVal, oldVal) {
      if (newVal.name === 'home') {
        const content = document.querySelector('#content')
        content.scrollTop = localStorage.getItem('position')
      }
    }
  }
}
</script>

<style lang="scss">
.van-swipe {
  height: 1.5rem;
  img {
    width: 100%;
  }
}
.backTop {
  position: fixed;
  bottom: 0.6rem;
  right: 15px;
  width: 30px;
  height: 30px;
  background-color: #fff;
  border-radius: 50%;
}
</style>
